<template>
  <main :style="{'margin-left':`${asideWidth}px`}">
    <section>
      <el-tabs v-model="tabId" type="card" closable @tab-remove="removeTab" @tab-click="clickTab">
        <el-tab-pane v-for="(tab) in tabArray" :key="tab.name" :label="tab.title" :name="tab.id">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>当前位置：{{tab.address}}</el-breadcrumb-item>
            <div class="stamp"></div>
          </el-breadcrumb>
          <iframe
            ref="iframeWindow"
            :src="tab.url"
            width="100%"
            height="100%"
            frameborder="0"
            scrolling="yes"
          ></iframe>
        </el-tab-pane>
      </el-tabs>
    </section>
  </main>
</template>
<script>
export default {
  name: "Aside",
  mounted() {},
  model: {
    prop: "tabId",
    event: "input"
  },
  props: {
    tabId: {
      type: String
    },
    asideWidth: {
      defalut: 200
    }
  },
  data() {
    return {
      tabArray: [],
      currentTabId: this.tabId
    };
  },
  methods: {
    clickTab(tabVM) {
      this.currentTabId = tabVM.$props.name;
    },
    addTab(tab) {
      console.log("tab", tab);
      let tabArray = [...this.tabArray];
      let isAddTab = tabArray.some(tabInArray => {
        return tabInArray.id === tab.id;
      });
      this.currentTabId = tab.id;
      if (isAddTab) {
        return false;
      }
      tabArray.push(tab);
      this.tabArray = tabArray;
    },
    removeTab(tabId) {
      let tabs = this.tabArray;
      // 如果与currentID相同，则调整currentID
      if (this.currentTabId === tabId) {
        tabs.forEach((tab, index) => {
          if (tab.id === tabId) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            if (nextTab) {
              this.currentTabId = nextTab.id;
            }
          }
        });
      }
      this.tabArray = tabs.filter(tab => tab.id !== tabId);
    }
  },
  watch: {
    currentTabId(id) {
      this.$emit("input", id);
    }
  },
  components: {}
};
</script>


<style lang="scss">
@mixin flex-full() {
  flex: 1;
  display: flex;
  flex-flow: column nowrap;
  justify-content: stretch;
}

@mixin border() {
  border: 1px solid #e4e7ed;
}

$tabs-item-height: 2rem;

main {
  // padding: 1rem;
  background-color: $main-background-color;
  @include flex-full();

  section {
    padding: 0.5rem;
    border-radius: 4px;
    background-color: white;
    @include elevation2();

    @include flex-full();
    .el-tabs {
      @include flex-full();
      // padding-left: 0.5rem;
      .el-tabs__header {
        margin: 0;
        padding: 0;
        border-bottom: none;
        .el-tabs__nav {
          z-index: 0;
          @include border();
          .el-tabs__item {
            position: relative;
            padding: 0.2rem 0.5rem;
            height: $tabs-item-height;
            line-height: 1.6rem;
            &::after {
              display: block;
              position: absolute;
              content: " ";
              bottom: 0;
              height: 2px;
              left: 50%;
              width: 0;
              background-color: $main-theme-color;
              transition: 0.1s ease-in;
            }
            &.is-active {
              &::after {
                display: block;
                position: absolute;
                content: " ";
                bottom: 0;
                height: 2px;
                left: 0;
                width: 100%;
                background-color: $main-theme-color;
              }
            }
          }
        }
      }
      .el-tabs__content {
        margin-top: 0.5rem;
        .el-breadcrumb {
          font-size: 0.8rem;
          padding: 0.5rem;
          z-index: 0;
          // @include border();
          @include elevation4();
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
          .stamp {
            flex: 1;
          }
        }
        @include flex-full();
        @include elevation2();
        .el-tab-pane {
          @include border();
          @include flex-full();
        }
      }
    }
  }
}
</style>

