<template>
  <aside>
    <el-button
      size="mini"
      :class="['toggle',{collapse:isCollapse},{expand:!isCollapse}]"
      @click="toggleMenuCollapse"
    >
      <i class="el-icon-menu"></i>
    </el-button>
    <el-menu @open="handleOpen" @close="handleClose" :collapse="isCollapse" ref="menu">
      <el-submenu :index="String(index)" v-for="(menu, index) in menuList" :key="index">
        <template slot="title">
          <i :class="menu.icon"></i>
          <span>{{menu.title}}</span>
        </template>
        <el-menu-item-group v-for="(submenu, indexSub) in menu[submenuList]" :key="indexSub">
          <el-menu-item
            :index="`${index}-${indexSub}`"
            @click="$parent.addTab(submenu)"
          >{{submenu.name}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </aside>
</template>
<script>
import $ from "jquery";

export default {
  name: "Aside",
  mounted() {
    console.log(this.$parent);
    $(this.$el).on("resize", event => {
      console.log(event);
    });
    // 初始化展开
    this.toggleMenuCollapse();
  },
  props: {
    isCollapse: {
      default: true
    },
    menuList: {
      type: Array,
      required: true
    },
    submenuList: {
      default() {
        return "submenuList";
      },
      type: String
    }
  },
  data() {
    return {};
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    toggleMenuCollapse() {
      let isCollapse = !this.isCollapse;
      this.$emit("update:isCollapse", isCollapse);
      this.$parent.asideWidth = isCollapse ? 65 : 200;
      console.log("this.$parent.asideWidth", this.$parent.asideWidth);
    }
  },
  computed: {
    toggleTitle() {
      return this.isCollapse ? "C" : "E";
    }
  },
  watch: {
    isCollapse() {
      let rect = this.$refs.menu.$el.getBoundingClientRect();
      this.$emit("asideChange", rect);
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
aside {
  position: relative;
  overflow: auto;
  overflow-x: hidden;
  .toggle {
    transition: 0.5s;
    z-index: 3;
    &.collapse {
      position: relative;
      // opacity: 1;
      left: 0;
      width: 65px;
      padding: 2px;
      text-align: center;
      &:hover {
        opacity: 1;
      }
    }
    &.expand {
      position: absolute;
      opacity: 0.5;
      left: 80%;
      top: 50%;
      &:hover {
        opacity: 1;
      }
    }
  }
  .el-menu {
    background-color: $aside-background-color;
  }
}
</style>

<style lang="scss">
aside {
  .el-menu {
    border-color: transparent;
  }
}
</style>

