import Vue from 'vue';
import InputSearchAddress from './InputSearchAddress.vue';

/**
 * 做一些实例化的操作
 */
export let addressSingleton = properties => {
    const props = properties || {};
    const Instance = new Vue({
        data: props,
        render(h) {
            return h(InputSearchAddress, {
                props: props
            });
        }
    });
    const component = Instance.$mount();
    document.body.appendChild(component.$el);
    const vm = Instance.$children[0];
    return vm
}