<template>
  <div class="input-search-address-wrapper">
    <el-dialog title="地址" :visible.sync="dialogFormVisible" center>
      <el-form :model="form">
        <template v-for="(item, index) in dsl">
          <el-form-item :label="item.label" :label-width="formLabelWidth" :key="'address'+index">
            <el-select v-model="form[item.prop]" placeholder="请选择区域">
              <el-option
                :label="dataItem.text"
                :value="dataItem"
                v-for="(dataItem, indexData) in dataCollection[item.prop]"
                :key="'dataItem'+indexData"
              ></el-option>
            </el-select>
          </el-form-item>
        </template>
        <el-form-item label="地址补充" :label-width="formLabelWidth">
          <el-input v-model="form.add" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  province,
  cityState,
  countyDistrict,
  villagesTowns,
  street,
  group
} from "./address.mock.js";
let dsl = [
  { prop: "province", value: "", label: "省" },
  { prop: "cityState", value: "", label: "市地州" },
  { prop: "countyDistrict", value: "", label: "区县" },
  { prop: "villagesTowns", value: "", label: "乡镇" },
  { prop: "street", value: "", label: "村/街道" },
  { prop: "group", value: "", label: "组" }
];

let dataCollection = {
  province: [],
  cityState: [],
  countyDistrict: [],
  villagesTowns: [],
  street: [],
  group: []
};

export default {
  name: "CInputSearchAddress",
  mounted() {
    /* 实际应该是级联选择，在上一级选择之后懒加载 */
    this.dataCollection.province = province;
    this.dataCollection.cityState = cityState;
    this.dataCollection.countyDistrict = countyDistrict;
    this.dataCollection.villagesTowns = villagesTowns;
    this.dataCollection.street = street;
    this.dataCollection.group = group;
  },
  data() {
    return {
      formLabelWidth: "120px",
      inputVM: null,
      dialogFormVisible: false,
      form: {
        province: "",
        cityState: "",
        countyDistrict: "",
        villagesTowns: "",
        street: "",
        group: "",
        add: ""
      },
      dsl,
      dataCollection
    };
  },
  methods: {
    init() {
      this.inputVM = null;
      Object.entries(this.form).map(entry => {
        /* 还有一些init设置 有一些值是根据当前账号来做限定处理*/
        console.log(this.$root);
        this.form[entry[0]] = "";
      });
    },
    open(inputVM) {
      this.init();
      this.inputVM = inputVM;
      this.dialogFormVisible = true;
    },
    save() {
      let vm = this;
      let availableArray = [];
      vm.dsl.forEach(item => {
        if (vm.form[item.prop] && vm.form[item.prop].text) {
          availableArray.push(vm.form[item.prop].text);
        }
      });
      vm.inputVM.currentValue =
        availableArray.join("-") + (vm.form.add ? "-" + vm.form.add : "");
      vm.inputVM.focus();
      vm.dialogFormVisible = false;
    }
  }
};
</script>

<style  lang="scss">
.input-search-address-wrapper {
  .el-select {
    width: 100%;
  }
}
</style>
